const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`; // abc-aaa
const qnameCapture = `((?:${ncname}\\:)?${ncname})`; // <aaa:asdads>
const startTagOpen = new RegExp(`^<${qnameCapture}`); // 标签开头的正则 捕获的内容是标签名
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // 匹配标签结尾的 </div>
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // 匹配属性的
const startTagClose = /^\s*(\/?)>/; // 匹配标签结束的 >  <div>
const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g




export function parseHTML(html) {
  let root = null; //ast 语法树的根
let currentParent; //识别当前父亲是谁
let stack = [];
const ELEMENT_TYPE = 1;
const TEXT_TYPE = 3;

function createASTElement(tagName, attrs) {
  return {
    tag: tagName,
    type: ELEMENT_TYPE,
    children: [],
    attrs,
    parent: null
  }
}

// 开始标签
function start(tagName, attrs) {
  let element = createASTElement(tagName, attrs)
  if (!root) {
    root = element;
  }
  // 把当前元素标记成父ast
  currentParent = element;
  stack.push(element)  //把标签存放在栈里
}
function end(tagName) {
  let element = stack.pop() //拿到的是ast对象
  //我要表示当前这个p属于这个div 的儿子
  currentParent = stack[stack.length - 1]
  if (currentParent) {
    element.parent = currentParent
    currentParent.children.push(element)  //实现了一个树的父子关系
  }

}
function chars(text) {
  text = text.replace(/\s/g, '')  //去掉所有空字符串
  if (text) {
    currentParent.children.push({
      text,
      type: TEXT_TYPE
    })
  }
}
  // 不停的去截取html字符串
  while (html) {
    let textEnd = html.indexOf('<')
    if (textEnd == 0) {
      // 当前索引为0 肯定是一个开始标签 
      let startTagMatch = parseStartTag()   // 通过这个方法获取匹配的结果 tagname attrs
      if (startTagMatch) {
        start(startTagMatch.tagName, startTagMatch.attrs)
        continue; // 如果开始标签匹配完毕后 继续下一次 匹配
      }
      let endTagMatch = html.match(endTag);
      if (endTagMatch) {
        advance(endTagMatch[0].length);  //删除结束标签
        end(endTagMatch[1])
        continue;
      }
    }
    let text;
    if (textEnd >= 0) {
      text = html.substring(0, textEnd)
    }
    if (text) {
      advance(text.length)  //删除文本属性
      chars(text)
    }
  }

  // 删除标签 
  function advance(n) {
    html = html.substring(n);

  }
  // 通过这个方法获取到匹配的结果 tagName,attrs
  function parseStartTag() {
    let start = html.match(startTagOpen)
    if (start) {
      const match = {
        tagName: start[1],
        attrs: []
      }
      advance(start[0].length)  //删除已经匹配的标签
      let end, attr;  //匹配结束标签  匹配属性
      while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
        // 将属性解析 
        advance(attr[0].length)  // 删除属性 
        match.attrs.push({
          name: attr[1],
          value: attr[3] || attr[4] || attr[5]
        })
      }
      if (end) {
        advance(end[0].length)
        return match;
      }


    }

  }

  return root;
}
